<template>
<div class="mt-4">
    <div class="callout callout-warning">
        <h5>Meta Floki Private Round!</h5>
        <p>The private sale is open to the public, please enter the amount of BNB you want to buy. If you want to buy with odd value (Example: 1.5 or 2.6) please use ''.'' between numbers.</p>
        <p>$MetaFloki will automatically transfer to your wallet right after the PinkSale IDO has sold out 24h.</p>
    </div>
    <div class="mt-5">
        <div class="card card-primary" style="max-width:650px;margin:auto;position: relative;">
            <form v-if="this.data.time < 0 || (user.amount && user.tx)">
                <div class="d-flex flex-column flex-sm-row justify-content-between w-100" style="padding:15px;color:#0032a8">
                    <h6 class="m-sm-2">Buy <strong>{{data.sy}} Coins</strong></h6>
                    <h6 class="m-sm-2"><strong><span class="auction-number">{{data.contri}}/{{data.contriMax}}</span></strong> Contributors</h6>
                </div>
                
                <div class="card-body text-center">
                    <p v-if="this.data.time < 0" style="margin-bottom:20px;">
                        <strong style="color:rgb(0, 50, 168);font-size:23px;">Private Sale completed</strong>
                    </p>
                    <p>{{data.contri}}/{{data.contriMax}} Contributors</p>
                    <p>{{data.bnbTotal}} BNB</p>
                    <p>{{Number(Math.round(data.bnbTotal/data.price)).toLocaleString('en')}} {{data.sy}}</p>
                    <p>Your balance: {{Math.round((user.amount||0)/data.price)}} {{data.sy}}</p>
                </div>
            </form>
            <form v-else>
                <div class="d-flex flex-column flex-sm-row justify-content-between w-100" style="padding:15px;color:#0032a8">
                    <h6 class="m-sm-2">Buy <strong>{{data.sy}} Coins</strong></h6>
                    <h6 class="m-sm-2"><strong><span class="auction-number">{{data.contri}}/{{data.contriMax}}</span></strong> Contributors</h6>
                </div>
                <h3 class="m-2 countdown-timer-heading text-center"><span class="text-dark">Current private sale </span><span class="text-primary font-weight-bold">closing in</span></h3>
                <h1 class="text-center text-dark font-weight-bold countdown-timer mt-3 mb-1">{{endTime}}</h1>
                <div class="progress-container w-100 p-sm-4">
                    <div class="progress-group">
                        <span class="progress-text">Visit Premium Page</span>
                        <span class="float-right"><b>{{data.contri}}/{{data.contriMax}}</b> Contributors</span>
                        <div class="progress progress-sm">
                            <div class="progress-bar bg-success" :style="`width: ${100*data.contri/data.contriMax}%`"></div>
                        </div>
                    </div>
                    <div class="d-flex flex-column flex-sm-row justify-content-between">
                        <span class="d-flex flex-column-reverse flex-sm-column ico-sale-token-sold-amount">
                            <span>Min:<span class="value"> {{data.min}}</span> BNB</span>
                        </span>
                        <span class="d-flex flex-column-reverse flex-sm-column ico-sale-token-for-sale-amount">
                            <span>Max: <span class="value">{{data.max}} </span> BNB</span>
                        </span>
                    </div>
                    <div class="d-flex flex-column flex-sm-row justify-content-between">
                        <span class="d-flex flex-column-reverse flex-sm-column ico-sale-token-sold-amount">
                            <span>Balance: <span class="value">{{balance}}</span> BNB</span>
                        </span>
                        <span class="d-flex flex-column-reverse flex-sm-column ico-sale-token-for-sale-amount">
                            <span>1BNB = <span class="value">{{Number(1/data.price).toLocaleString('en')}} </span> {{data.sy}}</span>
                        </span>
                    </div>
                </div>
                <div class="card-body text-right">
                    <div class="form-group">
                        <input v-model="amount" class="form-control">
                    </div>
                    <div class="form-group">
                        <button type="submit" class="btn btn-primary" style="width:100%;" @click="clickDeposit">Buy</button>
                    </div>
                </div>
            </form>
            <div v-if="pending" class="overlay d-flex justify-content-center align-items-center">
                <i class="fas fa-2x fa-sync fa-spin"></i>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import {
    mapState
} from 'vuex'
import detectEthereumProvider from '@metamask/detect-provider'
import axios from "axios"
export default {
    components: {},
    data() {
        return {
            amount: 0,
            pending: false,
            depositEvent: null,
            data: {
                contri: 70,
                contriMax: 150,
                min: 0.1,
                max: 3,
                price: 1 / 60000000000,
                bnbTotal: 141.3,
                sy: 'MetaFloki',
                dateEnd: 0,
                time: 0
            },
            endTime: `00h : 00m : 00s`
        }
    },
    computed: mapState({
        user: state => state.user,
        coinbase: state => state.web3.coinbase,
        balance: state => {
            if (state.web3.web3Instance !== null) return state.web3.web3Instance().fromWei(state.web3.balance, 'ether')
        }
    }),
    created() {
        this.load(this.getTime)
        setInterval(() => {
            this.endTime = this.getTime()
        }, 1000);
    },
    methods: {
        getTime() {
            this.data.time -= 1000
            let time = Math.ceil(this.data.time/1000)
            let d = lt(time / (24*60*60))
            let h = lt((time % (24*60*60)) / (60*60))
            let m = lt(time / 60 % 60)
            let s = lt(time % 60)
            function lt(h){
                return Math.round(h) > h ? Math.round(h) - 1 : Math.round(h)
            }
            return `${d}d :${h}h : ${m}m : ${s}s`
        },
        load(done) {
            axios.post("/api/users/privateSaleInfo")
                .then(rs => {
                    if (rs.data.data) Object.assign(this.data, rs.data.data)
                    if (done) done()
                })
                .catch(e => {
                    if (done) done()
                })
        },
        updateInfo(o, done) {
            axios.post("/api/users/updateInfo", {
                    wallet: this.$store.state.web3.coinbase,
                    amount: o.amount,
                    tx: o.tx
                })
                .then(rs => {
                    this.$store.commit('setInfo', rs.data.data)
                    this.data.contri++
                    this.data.bnbTotal += parseFloat(this.amount)
                    done()
                })
                .catch(e => {
                    done()
                })
        },
        connectMetamask: async (store) => {
            try {
                const provider = await detectEthereumProvider()
                if (provider) {
                    const accounts = await provider.request({
                        method: 'eth_requestAccounts'
                    })
                    if (accounts && accounts[0] && accounts[0] != store.state.web3.coinbase) window.location.reload()
                }
            } catch (err) {
                console.error('Connect metamask fail:', err)
            }
        },
        clickDeposit(event) {
            if (!this.$store.state.web3 || !this.$store.state.web3.coinbase) {
                this.connectMetamask(this.$store)
                return
            }
            if (this.$store.state.web3.networkId != 56 || isNaN(this.amount) || this.amount < this.data.min || this.amount > this.data.max) return
            this.depositEvent = null
            this.pending = true
            this.$store.state.vaultContractInstance().deposit({
                gas: 300000,
                value: this.$store.state.web3.web3Instance().toWei(this.amount, 'ether'),
                from: this.$store.state.web3.coinbase
            }, (err, result) => {
                if (err) {
                    this.pending = false
                } else {
                    let _this = this
                    checkBalance(function () {
                        _this.updateInfo({
                            tx: result,
                            amount: _this.amount
                        }, function () {
                            _this.pending = false
                        })
                    })

                    let balance = this.balance

                    function checkBalance(done) {
                        setTimeout(() => {
                            if (balance == _this.balance) checkBalance(done)
                            else done()
                        }, 500);
                    }
                }
            })
        }
    }
}
</script>



